/* eslint-disable no-param-reassign */
import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { Phones } from '../../services/Phone';

export const loadPhones = createAsyncThunk('phones/loadPhones', async () => {
  const response = await fetch('/api/products.json');
  const data = await response.json();

  return data.filter(
    (phone: { category: string }) => phone.category === 'phones',
  );
});

export interface PhoneState {
  phones: Phones[];
  isLoading: boolean;
  isError: boolean;
}

const initialState: PhoneState = {
  phones: [],
  isLoading: false,
  isError: false,
};

export const phonesSlice = createSlice({
  name: 'phones',
  initialState,
  reducers: {},

  extraReducers: builder => {
    builder
      .addCase(loadPhones.pending, state => {
        state.isLoading = true;
      })
      .addCase(loadPhones.fulfilled, (state, action) => {
        state.isLoading = false;
        state.phones = action.payload;
      })
      .addCase(loadPhones.rejected, state => {
        state.isLoading = false;
        state.isError = true;
      });
  },
});

export default phonesSlice.reducer;
