import React, { useState } from 'react';
import styles from './banner.module.scss';
import Banner from './Banner-img/Banner.png';
import Banner2 from './Banner-img/banner-phones.png';
import Banner3 from './Banner-img/banner-tablets.png';
import sliderButton from './Banner-img/sliderButton.png';
// eslint-disable-next-line import/no-extraneous-dependencies
// import SwiperCore, { Autoplay } from 'swiper';
// eslint-disable-next-line no-redeclare, @typescript-eslint/no-redeclare
// eslint-disable-next-line import/no-extraneous-dependencies
import 'swiper/css';
import { Swiper, SwiperSlide } from 'swiper/react';

// SwiperCore.use([Autoplay]);

export const Baner: React.FC = () => {
  const [activeSlide, setActiveSlide] = useState(0);
  const banners = [Banner, Banner2, Banner3];

  const nextSlide = () => {
    setActiveSlide((prevSlide: number) => (prevSlide + 1) % banners.length);
  };

  const prevSlide = () => {
    setActiveSlide(
      // eslint-disable-next-line @typescript-eslint/no-shadow
      (prevSlide: number) => (prevSlide - 1 + banners.length) % banners.length,
    );
  };

  return (
    <>
      <section className={styles.sectionBanner}>
        <button className={styles.bannerButtons} onClick={prevSlide}>
          <img className={styles.sliderArrowLeft} src={sliderButton}></img>
        </button>
        <div className={styles.bannerContent}>
          {/* <img className={styles.bannerImg} src={Banner}></img> */}
          <Swiper
            className={styles.bannerImg}
            spaceBetween={50}
            slidesPerView={1}
            onSlideChange={nextSlide}
            loop={true}
          >
            {banners.map((banner, index) => (
              <SwiperSlide key={index} className="swiper-w">
                <img
                  src={banner}
                  alt={`Banner ${index + 1}`}
                  className={styles.bannerImg}
                />
              </SwiperSlide>
            ))}
          </Swiper>
          <svg
            className={styles.bannerDotters}
            xmlns="http://www.w3.org/2000/svg"
            width="80"
            height="24"
            viewBox="0 0 80 24"
            fill="none"
          >
            <rect
              x="5"
              y="10"
              width="14"
              height="4"
              fill={activeSlide === 0 ? '#000' : '#E2E6E9'}
            />
            <rect
              x="33"
              y="10"
              width="14"
              height="4"
              fill={activeSlide === 1 ? '#000' : '#E2E6E9'}
            />
            <rect
              x="61"
              y="10"
              width="14"
              height="4"
              fill={activeSlide === 2 ? '#000' : '#E2E6E9'}
            />
          </svg>
        </div>
        <button className={styles.bannerButtons} onClick={nextSlide}>
          <img className={styles.sliderArrowRight} src={sliderButton}></img>
        </button>
      </section>
    </>
  );
};
